.blh-danmuku {
  position: fixed;
  z-index: 9999;
  top: 10px;
  right: 10px;
  width: 280px;
  font-size: 13px;
  user-select: none;
  font-family: Hiragino Sans GB, Microsoft YaHei, \5fae\8f6f\96c5\9ed1, tahoma, arial, simsun, \5b8b\4f53;
  text-size-adjust: 100% !important;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  line-height: 1;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6); }
  .blh-danmuku *,
  .blh-danmuku *::before,
  .blh-danmuku *::after {
    box-sizing: border-box; }
  .blh-danmuku ::-webkit-scrollbar {
    display: block !important;
    width: 5px !important; }
  .blh-danmuku ::-webkit-scrollbar-thumb {
    background-color: #ccc !important; }
  .blh-danmuku ::-webkit-scrollbar-thumb:hover {
    background-color: #eee !important; }
  .blh-danmuku .blh-header {
    display: flex;
    height: 25px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    color: rgba(255, 255, 255, 0.8); }
    .blh-danmuku .blh-header .blh-header-l {
      display: flex;
      align-items: center;
      flex: 1;
      cursor: move;
      padding: 0 10px;
      font-size: 13px; }
    .blh-danmuku .blh-header .blh-header-r {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 25px;
      font-size: 18px;
      cursor: pointer; }
  .blh-danmuku .blh-danmu {
    padding: 10px;
    min-height: 100px;
    height: 500px;
    overflow-y: auto;
    scroll-behavior: smooth;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
    .blh-danmuku .blh-danmu .blh-danmu-inner .blh-danmu-item {
      text-align: left;
      line-height: 1.4;
      margin-bottom: 10px;
      text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1); }
      .blh-danmuku .blh-danmu .blh-danmu-inner .blh-danmu-item .blh-danmu-uname {
        color: #cae8ff; }
      .blh-danmuku .blh-danmu .blh-danmu-inner .blh-danmu-item .blh-danmu-text {
        word-wrap: break-word; }
  .blh-danmuku .blh-gift {
    padding: 10px;
    height: 100px;
    overflow-y: auto;
    scroll-behavior: smooth; }
    .blh-danmuku .blh-gift .blh-gift-inner .blh-gift-item {
      text-align: left;
      line-height: 1.4;
      margin-bottom: 10px;
      text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1); }
      .blh-danmuku .blh-gift .blh-gift-inner .blh-gift-item .blh-gift-uname {
        color: #cae8ff; }
  .blh-danmuku .blh-footer {
    height: 10px;
    cursor: ns-resize; }

.blh-danmuku-icon {
  display: none;
  position: fixed;
  z-index: 9999;
  top: 10px;
  right: 10px;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  font-size: 13px;
  user-select: none;
  font-family: Hiragino Sans GB, Microsoft YaHei, \5fae\8f6f\96c5\9ed1, tahoma, arial, simsun, \5b8b\4f53;
  text-size-adjust: 100% !important;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  cursor: pointer;
  opacity: 0.2;
  color: #fff;
  background-color: #000; }
